import React from "react";

class Gallery extends React.Component {
    render() {
        return (
            <div className="box">
                <div>
                    <h1>Gallery</h1>
                </div>
                <Images />
            </div>
        )
    }
}

class Images extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            imgid: 0
        }
    }

    renderImg(i){
        return(
            <img src={[i + ".png"]} id={[i]} value={[i]} className={"maingallery"} width={"680px"} height={"380px"}/>
        )
    }

    renderMainImg(){
        const x = this.state.imgid;
        return(
            <img src={[x + ".png"]} id={[x]} value={[x]} className={"maingallery"} width={"680px"} height={"380px"}/>
        )
    }

    handleClick(i){
        this.setState({
            imgid: i,
        });
    }

    render() {
        return (
            <div>
                <div>
                    {this.renderMainImg()}
                </div>
                <div className={"subgallery"}>
                    <div onClick={() => this.handleClick(0)}>
                        {this.renderImg(0)}
                    </div>
                    <div onClick={() => this.handleClick(1)}>
                        {this.renderImg(1)}
                    </div>
                    <div>
                        <img src="img0.png" id="img" className={"maingallery"} width={"680px"} height={"380px"}/>
                    </div>
                    <div>
                        <img src="img0.png" id="img" className={"maingallery"} width={"680px"} height={"380px"}/>
                    </div>
                </div>
                <div className={"subgallery"}>
                    <div>
                        <img src="img0.png" id="img" className={"maingallery"} width={"680px"} height={"380px"}/>
                    </div>
                    <div>
                        <img src="img0.png" id="img" className={"maingallery"} width={"680px"} height={"380px"}/>
                    </div>
                    <div>
                        <img src="img0.png" id="img" className={"maingallery"} width={"680px"} height={"380px"}/>
                    </div>
                    <div>
                        <img src="img0.png" id="img" className={"maingallery"} width={"680px"} height={"380px"}/>
                    </div>
                </div>
            </div>
        )
    }
}


export default Gallery;